import React from 'react';
import Component from 'react-pure-render/component';
import InnerPage from 'routes/InnerPage';
import Feedback from 'components/Feedback';
import links from 'data/links.yml';

export default class XsollaPage extends Component {

  render() {
    return (
      <InnerPage>
        <div className={styles.root}>
          <div className={styles.content}>
            <div className={styles.text1}>графический дизайн</div>
            <h2 className={styles.text2}><b>Иллюстрации</b> <br /> для
              <a href={links.xsolla} target='_blank'> "Икcоллы"</a></h2>
            <div className={styles.logo}/>
            <div className={styles.cols}>
              <div className={styles.text3}>
                <p><b>Задача</b> <br /> Разработать оформление маркетинговой кампании.</p><br />
                <p>“Иксолла” — крупная международная платежная система по приему онлайновых игровых платежей. Регулярно
                  компания проводит акции, направленные на популяризацию тех или иных аспектов своей деятельности.
                  Используются разные каналы коммуникации, от социальных сетей, до публикаций статей на профильных
                  сайтах.
                  Всему многообразию форматов требуется красивое оформлении.</p><br />
                <p>В “Кайндбитле” создали набор иллюстраций, рассказывающий о локальных платежах.</p>
                <p className={styles.text4}>1 января 2016</p>
              </div>
              <div className={styles.divX}>
                <div className={styles.img1}></div>
              </div>
            </div>
          </div>
        </div>
        <div className={styles.img2}></div>
        <div className={styles.root}>
          <div className={styles.content2}>
            <div className={styles.text5}>
              <p>Основным мотивом стали многообразие способов потратить деньги на игры по всему миру и различия в
                жизни
                каждого конкретного игрока.</p>
            </div>
            <div className={styles.img3}></div>
            <div className={styles.img4}></div>
            <div className={styles.img5}></div>
            <div className={styles.img6}></div>
            <div className={styles.img7}></div>
          </div>
        </div>
        <div className={styles.border}></div>
        <Feedback/>
      </InnerPage>
    );
  }

}



