import React from 'react';
import Component from 'react-pure-render/component';
import InnerPage from 'routes/InnerPage';
import Feedback from 'components/Feedback';
import links from 'data/links.yml';

export default class XsollaPage extends Component {

  render() {
    return (
      <InnerPage>
        <div className={styles.root}>
          <div className={styles.content}>
            <div className={styles.text1}>графический дизайн</div>
            <h2 className={styles.text2}><b>Фирменный стиль</b> <br /> «Иннополиса»</h2>
            <div className={styles.logo}/>
            <div className={styles.cols}>
              <div className={styles.text3}>
                <p><b>Задача</b> <br /> Разработать фирменный стиль для нового города.</p><br />
                <p>Это вторая версия фирменного стиля для Иннополиса.
                </p><br />
                <p>В “Кайндбитле” придумали футуристический конструктор техногородов.</p>
                <p className={styles.text4}>1 января 2016</p>
              </div>
              <div className={styles.divX}>
                <div className={styles.img1}></div>
              </div>
            </div>
          </div>
        </div>
        <div className={styles.img2}></div>
        <div className={styles.root}>
          <div className={styles.content2}>
            <div className={styles.text5}>
              <p>В основе знака идея новой территории, города будущего. Объекты удивительных форм - это разно-образные
                здания. Они могут изменяться, их число может расти вместе с развитием Иннополиса. Знак строится из
                разных комбинаций узнаваемых элементов.
              </p>
            </div>
            <div className={styles.img3}></div>
            <div className={styles.img4}></div>

            {/*<div className={styles.imgGroup}>*/}
              {/*<div className={styles.imgContainer1}>*/}
                {/*<div className={styles.img4}></div>*/}
              {/*</div>*/}
              {/*<div className={styles.imgContainer2}>*/}
                {/*<div className={styles.img5}></div>*/}
              {/*</div>*/}
            {/*</div>*/}
          </div>
        </div>
        <div className={styles.root}>
          <div className={styles.content2}>
            <div className={styles.text5}>
              <p>Герб и флаг развивают идею знака, упрощая здания до фигур-символов. Три элемента на гербе и флаге
                олицетворяют постоянное развитие города через смену форм.</p>
            </div>
          </div>
        </div>
        <div className={styles.img6}></div>
        <div className={styles.root}>
          <div className={styles.content2}>
            <div className={styles.text5}>
              <p>В “Кайндбитле” был разработан <a href={links.innopolis1}>ещё один концепт</a> фирменного стиля для
                Иннополиса.</p>
            </div>
          </div>
        </div>
        <div className={styles.border}></div>
        <Feedback />
      </InnerPage>
    );
  }

}



