import React from 'react';
import Component from 'react-pure-render/component';
import InnerPage from 'routes/InnerPage';
import Feedback from 'components/Feedback';

export default class XsollaPage extends Component {

  render() {
    return (
      <InnerPage>
        <div className={styles.root}>
          <div className={styles.content}>
            <div className={styles.text1}>графический дизайн</div>
            <h2 className={styles.text2}><b>Фирменный стиль</b> <br /> «Иннополиса»</h2>
            <div className={styles.logo}/>
            <div className={styles.cols}>
              <div className={styles.text3}>
                <p><b>Задача</b> <br /> Разработать фирменный стиль для нового города.</p><br />
                <p>Иннополис — новый российский город, расположенный в Республике Татарстан, призванный стать центром
                  развития высоких технологий и местом жизни молодых специалистов.<br />
                  В Иннополисе есть собственная городская экосистема, пять тысяч местных жителей и амбициозные планы на
                  ближайшее будущее.
                </p><br />
                <p>В “Кайндбитле” разработали фирменный стиль и герб, призванный помочь всем начинаниям свершиться.</p>
                <p className={styles.text4}>1 января 2016</p>
              </div>
              <div className={styles.divX}>
              <div className={styles.img1}></div>
                </div>
            </div>
          </div>
        </div>
        <div className={styles.img2}></div>
        <div className={styles.root}>
          <div className={styles.content2}>
            <div className={styles.text5}>
              <p>Основная идея знака - графические образы микросхемы и щита. Микросхема как единица и суть всех
                электронных технологий. Щит как древний символ защиты и укрепления.</p>
            </div>
            <div className={styles.img3}></div>
            <div className={styles.text5}>
              <p>Крылатый снежный барс - один из символов Татарстана. Поэтому за основу герба Иннополиса решено было
                взять именно его. Ак барс держит в лапах ноутбук, как один из инструментов коммуникации, без которого не
                обходится ни один специалист.</p>
            </div>
          </div>
        </div>
        <div className={styles.img4}></div>
        <div className={styles.root}>
          <div className={styles.content2}>
            <div className={styles.text5}>
              <p>В “Кайндбитле” также разработали ещё один концепт фирменного стиля для Иннополиса.</p>
            </div>
          </div>
        </div>
        <div className={styles.border}></div>
        <Feedback />
      </InnerPage>
    );
  }

}



